.TodoList {
    margin: 4rem auto;
    padding: 2rem 3rem 3rem;
    max-width: 500px;
    background-color: #ff6666;
    color: #fff;
    box-shadow: -10px -10px 0px 0px rgba(100, 100, 100, 0.1);
    border-radius: 10px;
}

.TodoList h1 {
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05em;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.TodoList ul {
    margin-top: 2.6rem;
    list-style: none;
}
